            <line id="XMLID_14_" class="st1" x1="-344.1" y1="241.8" x2="-344.1" y2="320"/>
            <line id="XMLID_15_" class="st1" x1="-378" y1="183.2" x2="-276.5" y2="241.8"/>
            <line id="XMLID_16_" class="st1" x1="-276.5" y1="241.7" x2="-276.5" y2="280.9"/>
