            <line id="XMLID_18_" class="st1" x1="-276.5" y1="359" x2="-242.7" y2="339.5"/>
            <line id="XMLID_19_" class="st1" x1="-276.5" y1="241.7" x2="-208.8" y2="202.8"/>
            <line id="XMLID_20_" class="st1" x1="-208.8" y1="202.6" x2="-175" y2="183.3"/>
            <line id="XMLID_21_" class="st1" x1="-208.8" y1="241.7" x2="-208.8" y2="319.8"/>
