          </g>
        </svg>
      </div>
    </div>
    <div class="col">
      <div class="menu row">
        <ul>
          <li v-for="link in navLinks"><span class="linkUnderline">{{ link }}</span></li>
        </ul>
        <i class="material-icons hamburger">menu</i>
      </div>
    </div>
  </nav>
</div>
</template>

<script>
export default {
  data () {
    return {
      navLinks: ['Home', 'Work', 'Blog', 'About', 'Contact']
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/main.scss";

li {
  cursor: pointer;
  display: inline;
  padding-left: 3em;
}

nav {
  align-items: center;
  color: $color-white;
  display: flex;
  flex: 1 1 auto;
  font-family: 'Montserrat', sans-serif;
  justify-content: space-between;
  max-width: 1080px;
}

.container-nav {
  border-bottom: 2px solid $color-accent;
  background-color: $color-primary-dark;
  display: flex;
  justify-content: center;
  padding: 1.25em;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.hamburger {
  cursor: pointer;
  display: none;
  font-size: 2.5em;
  transition: color $timing ease-in-out;
}

.menu {
  align-items: center;
  justify-content: flex-end;
  padding-right: 3em;
}

.nav-logo svg {
  cursor: pointer;
  height: 35px;
}

.nav-logo svg .st0 {
  transition: fill $timing ease-in-out;
}

.st0 {
  fill:$color-accent;
}

.st1 {
  fill:none;
}

.nav-logo:hover .st0 {
  fill: $color-white;
}

@media (max-width: 600px) {
  ul {
    display: none;
  }

  .hamburger {
    display: block;
  }
}

@media (min-width: 600px) {
  .hamburger {
    color: $color-white;
  }
}

</style>



